body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.github-profile {
  margin: 1rem;
  }

.header {
  text-align: center;
  color: chocolate;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.info {
  display: inline-block;
  margin-left: 12px;
}

.firstname {
  font-size: 1rem;
}

.lastname {
  font-size: 1rem;
}

.age {
  font-size: 1rem;
}

.gender {
  font-size: 1rem;
}

.dob {
  font-size: 1rem;
}

.phonenumber {
  font-size: 1rem;
}

.birthplace {
  font-size: 1rem;
}

.status {
  font-size: 1rem;
}
